
import { defineComponent, ref } from '@vue/composition-api';
import Jsonschema2Form from '@4dst-saas/form-utils/dist/components/jsonschema2form';
import Jsonschema from '@4dst-saas/form-utils/dist/components/jsonschema2form/JSONschema';
import TagsEditor from '@/components/base/TagsEditor.vue';
import schema from './shoeInfoSchema';


const testForm = defineComponent({
  name: 'testForm',
  components: {
    TagsEditor,
  },
  props: {
    jsonschema: {
      type: Array as () => Jsonschema[],
      default: () => schema,
    },

  },
  setup() {
    const form = ref({});
    return {
      form,
    };
  },

  render() {
    const j2f = <Jsonschema2Form ref='formRef' jsonschema={this.jsonschema}
      class="flex-wrap"
      size="small"
      form={this.form}
      labelPosition='top'
      showValidateButton>
    </Jsonschema2Form>;
    const style = {
      height: '200px',
      width: '100%',
    };
    return this.jsonschema ? j2f : <div vLoading={true} style={style}>
      <div vShow={false}>{j2f}</div>
    </div>;
  },
});

export default testForm;
